import React from "react";
import { connect } from "react-redux";

function Context({ context }) {
  return (
    <div>
      <h1>{context}</h1>
      <p>nr of players:</p>
    </div>
  );
}

function mapStateToProps(state) {
  return { context: state.context };
}

export default connect(mapStateToProps)(Context);



